body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.error-template {
  padding: 40px 15px;
  text-align: center;
}
.error-actions {
  margin-top: 15px;
  margin-bottom: 15px;
}
.error-actions .btn {
  margin-right: 10px;
}

.login-centered {
  margin-top: 10%;
}

.form-alert {
  position: absolute;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}

.user-form {
  min-width: 400px;
}

.left-controls {
  min-width: 400px;
  max-width: 400px;
}

.borders {
  position: absolute;
  overflow: visible;
}

.status {
  margin-bottom: 10px;
}

.board {
  margin: auto;
  line-height: 0rem;
  background: repeat-linear-gradient(
        90deg,
        transparent calc(50% - 1px),
        #000000,
        transparent calc(50% + 1px)
      )
      bottom,
    linear-gradient(180deg, transparent calc(50% - 1px), #000000, transparent calc(50% + 1px)) top;
}

.cell-red,
.cell-blue,
.cell-empty {
  height: 18px;
  width: 18px;
  border-radius: 50%;
  margin-top: 2px;
  display: inline-block;
}

.cell-red {
  background-color: red;
}

.cell-blue {
  background-color: blue;
}

.cell-empty {
  background-color: transparent;
}

.cell-active {
  cursor: pointer;
}

.cell-wrapper {
  position: relative;
  content: '';
  line-height: 20px;
  height: 24px;
  width: 24px;
  text-align: center;
  display: inline-block;
}

.cell-wrapper:before {
  border-top: 2px rgba(0, 0, 0, 0.103) solid;
  width: 24px;
  display: block;
  content: '';
  top: 10px;
  position: absolute;
  z-index: -1;
}

.cell-wrapper:after {
  border-left: 2px rgba(0, 0, 0, 0.103) solid;
  height: 24px;
  display: block;
  content: '';
  left: 12px;
  top: 0px;
  position: absolute;
  z-index: -1;
}

.border-blue {
  fill: rgba(0, 0, 0, 0);
  stroke: rgb(19, 19, 248);
  stroke-width: 5;
}

.border-red {
  fill: rgba(0, 0, 0, 0);
  stroke: rgba(248, 19, 19, 1);
  stroke-width: 5;
}

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid black;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
